body {
  margin: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@import "bootstrap/dist/css/bootstrap.css";

* {
  font-family: "Montserrat", sans-serif;
}

.h_00 {
  font-size: 45px;
  font-weight: bold;
}
.br10 {
  border-radius: 10px;
  border-color: none;
}
.basicColor {
  background-color: #0094c6;
}
.shadowbox {
  box-shadow: 10px 10px 5px #ccc;
  -moz-box-shadow: 10px 10px 5px #ccc;
  -webkit-box-shadow: 10px 10px 5px #ccc;
  -khtml-box-shadow: 10px 10px 5px #ccc;
}
.textColor{
  color: #0094c6;
}
.marginBig{
  margin-top: 20%;
}
.w80{
  width:90%;
}
.secondColor{
  background-color: #CCC9DC;
}

.scrolls{
    overflow-x: scroll;
    overflow-y: hidden;
    white-space:nowrap;
    width: 100%;
}

.scrolls::-webkit-scrollbar {
  height: 8px;              /* height of horizontal scrollbar ‚Üê You're missing this */
  width: 10px;               /* width of vertical scrollbar */
  background-color: #CCC9DC;
}

.scrolls::-webkit-scrollbar-thumb{
  background-color: #0094c6;
  border-radius: 10px;
}

.grids{
  transition: transform .2s;
  z-index: 0;
}
.grids:hover{
  -ms-transform: scale(1.1); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(1.1); 
  border-radius: 10px;
  background-color:#CCC9DC;
  z-index: 100;
  font-weight: bold;
}

.bigGrid{
  transition: transform .2s;
  z-index: 0;
}
.bigGrid:hover{
  -ms-transform: scale(1.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.05);
  z-index: 100;
  font-weight: bold;
}

.circle{
  border-radius: 50%;
  background-color: transparent;
  border: 3px solid #0094c6;
  height: 60px;
  width: 60px;
}
.workGrid{
  width:800px;
  height: 230px;
}
.vertical{
  height: 200px;
  border:1px solid #0094c6;
  width: 3px;
  border-radius: 10px;
  background-color: #0094c6;
  position: relative;
  left: 50%;
}
.active{
  background-color: #0094c6 !important;
  border: none !important;
}
.last{
  height: 170px !important;
}
.inputContact{
  background-color: transparent;
  border: none;
  border-bottom: 1px solid white;
}
.form-control{
  font-size: 16px;
}
.form-control::placeholder{
  color: #0094c6;
}
.circleButton{
  border-radius: 50%;
  background-color: white;
  height: 100px;
  width: 100px;
  position: relative;
}
.circleButton img{
  position: absolute;
    width:60px;
    height: auto;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}

.social img{
  width: 35px;
  height: auto;
}

.githubButton{
  background-color: #0094c6;
  border-radius: 10px;
  width: 180px;
  cursor: pointer;
  position: relative;
  top:10px;
}